import React, { createRef, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import { Message } from './useDialogue';

const Container = styled.div`
	background-color: #edf5fd;
	width: 100%;
	display: flex;
	flex-direction: column;
	flex-flow: column;
	padding: 1rem;
	gap: 1rem;
	flex: 1 1 auto;
	overflow-y: auto;
	min-height: 0px;
	padding-bottom: 7rem;
`;

const Name = styled.div`
	height: 30%;
	font-weight: bold;
`;

const Text = styled.div`
	height: 90%;
`;

const Bubble = styled.div`
	min-height: 3rem;
	padding: 1rem;
	max-width: 60%;
	flex-shrink: 0;
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
`;

const RobotMessage = styled(Bubble)`
	border-radius: 15px 15px 15px 0px;
	background-color: #cecece;
`;

const HumanMessage = styled(Bubble)`
	border-radius: 15px 0px 15px 15px;
	align-self: flex-end;
	background-color: #579ffb;
	color: white;

	& > ${Name} {
		align-self: flex-end;
	}
`;

const TinkingBubble = styled(RobotMessage)`
	display: flex;
	flex-direction: row;
	align-items: flex-end;
	padding-bottom: 0.7rem;
	max-width: 5rem;
`;

const bounce = keyframes`
    0% {
        transform: translate(0, 0);
	background-color: #000000b0;

    }
    25% {
        transform: translate(0,-1rem);
	background-color: #00000050;

    }
    50% {
        transform: translate(0, 0);
	background-color:#000000b0;

    }
`;

const Ball = styled.div`
	border-radius: 50%;
	width: 0.7rem;
	height: 0.7rem;
	background-color: #000000b0;
	animation: ${bounce} 1.5s linear infinite;
`;

const Ball1 = styled(Ball)`
	animation-delay: 0s;
`;

const Ball2 = styled(Ball)`
	animation-delay: 0.3s;
`;

const Ball3 = styled(Ball)`
	animation-delay: 0.6s;
`;

const EmptyText = styled.p``;

interface ChatProps {
	dialogue: Message[];
	isThinking: boolean;
}
const Chat = ({ dialogue, isThinking }: ChatProps) => {
	const containerRef = createRef<HTMLDivElement>();

	useEffect(() => {
		if (containerRef.current) {
			containerRef.current.scrollTop = containerRef.current.scrollHeight;
		}
	}, [dialogue]);

	return (
		<Container ref={containerRef}>
			{dialogue.map(({ type, text }, i) => (
				<React.Fragment key={i}>
					{type === 'human' ? (
						<HumanMessage>
							<Name>Human</Name>
							<Text>{text}</Text>
						</HumanMessage>
					) : (
						<RobotMessage>
							<Name>Da Vinci AI</Name>
							<Text>{text}</Text>
						</RobotMessage>
					)}
				</React.Fragment>
			))}
			{isThinking ? (
				<TinkingBubble>
					<Ball1 />
					<Ball2 />
					<Ball3 />
				</TinkingBubble>
			) : (
				''
			)}
			{dialogue.length === 0 ? (
				<EmptyText>
					Click one of the microphone buttons to start talking with the AI!
				</EmptyText>
			) : (
				''
			)}
		</Container>
	);
};
export default Chat;
