import { Configuration, OpenAIApi } from 'openai';
import { useState } from 'react';

const useOpenAi = () => {
	const separators = ['&htxtmsg$:', '&rtxtmsg$:']; //[human is speaking indicator, robot is speaking indicator]
	const [transcriptList, setTrascriptList] = useState<string[]>([]);
	const [isThinking, setIsThinking] = useState(false);

	const talk = async (prompt: string) => {
		setIsThinking(true);
		const configuration = new Configuration({
			apiKey: process.env.REACT_APP_OPENAI_KEY,
		});
		const openai = new OpenAIApi(configuration);

		const response = await openai.createCompletion({
			model: 'text-davinci-003',
			prompt,
			temperature: 0.9,
			max_tokens: 150,
			top_p: 1,
			frequency_penalty: 0,
			presence_penalty: 0.6,
			stop: separators,
		});

		setIsThinking(false);
		setTrascriptList([...transcriptList, response.data.choices[0].text || '']);
		return response;
	};

	return { talk, transcriptList, separators, isThinking };
};

export default useOpenAi;
